import React, { useState } from 'react';
import AddCompanyPage from './add-company-page';
import { Organization } from '../../../types/organization';

const AddCompanyPageContainer = () => {
  const [value, setValue] = useState<Organization>({
    id: 343438483,
    inn: '',
    affiliate_number: '',
    kpp: '',
    ogrn: '',
    name: '',
    kis_art_id: '431313813138',
    contract_status: 'Заключен',
    region: '',
    dealer_id: 13138183,
    dealer: { id: 3513, name: '' },
    dealers_responsible_id: 13183183,
    contacts: '',
    comments: '',
    price_type: undefined,
    price: undefined,
    tech_inspection_price: undefined,
    dealer_price: undefined,
    tech_inspection_dealer_price: undefined,
    sett_local_time: false,
    sett_org_billing: false,
    sett_sms: false,
    responsible_name: '',
    responsible_phone: '',
    sett_temperature_in_degrees: false,
    sett_autoinspect: false,
    sett_repeats: false,
    sett_face_id: false,
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    setValue({ ...value, [e.target.name]: e.target.value });
  };

  const selectChange = (e: React.ChangeEvent<{ name?: string | undefined; value: unknown }>) => {
    e.preventDefault();
    setValue({ ...value, price_type: e.target.value === '0' ? 0 : 1 });
  };

  const selectContractStatus = (
    e: React.ChangeEvent<{ name?: string | undefined; value: unknown }>,
  ) => {
    e.preventDefault();
    setValue({ ...value, price_type: e.target.value === '0' ? 0 : 1 });
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    alert(JSON.stringify(value));
  };
  return (
    <AddCompanyPage
      value={value}
      handleSubmit={handleSubmit}
      handleChange={handleChange}
      selectChange={selectChange}
      selectContractStatus={selectContractStatus}
    />
  );
};

export default AddCompanyPageContainer;
